import React, { ReactElement, FC } from 'react'
import clsx from 'clsx'
import { toast, ToastContainer, ToastOptions } from 'react-toastify'
import { AlarmIcon } from '../../icons/Alarm'
import { CloseCrossIcon } from '../../icons/CloseCross'
import { ApprovedIcon } from '../../icons/Approved'
import { WarningCircleIcon } from '../../icons/WarningCircleIcon'
import { MessageStatusType, MessageProps } from './types'
import { Typography } from '../Typography'
import './Notification.scss'

const icons: Record<MessageStatusType, ReactElement> = {
  success: <ApprovedIcon />,
  error: <AlarmIcon />,
  warning: <WarningCircleIcon />,
  button: <ApprovedIcon height={20} width={20} />,
}

const Msg: FC<MessageProps> = ({
  content,
  status,
  confirmText,
  onConfirmClick,
}) => {
  const onClick = (evt): void => {
    evt.stopPropagation()
    onConfirmClick!()
  }

  const shouldRenderButtons = Boolean(onConfirmClick)

  return (
    <div
      className={clsx('Message__Container', {
        '--with-btn': shouldRenderButtons,
      })}
    >
      <div className={`Message --${status}`}>
        {status && (
          <div className='Message__Icon --Status'>{icons[status]}</div>
        )}
        <Typography
          className={`Message__Content --${status}`}
          text={content}
          type='Body1'
        />
        {shouldRenderButtons && (
          <div className='Message__ButtonsWrapper'>
            <button className='Message__Button --confirm' onClick={onClick}>
              {confirmText}
            </button>
          </div>
        )}
        <div
          className={clsx('Message__Icon --Close', {
            '--with-btn': shouldRenderButtons,
          })}
        >
          <CloseCrossIcon />
        </div>
      </div>
    </div>
  )
}

export const notify = (
  text: string,
  status: MessageStatusType,
  buttons?: Pick<MessageProps, 'cancelText' | 'confirmText' | 'onConfirmClick'>,
  options: ToastOptions = {},
): void => {
  switch (status) {
    case 'success':
      toast.success(<Msg content={text} status='success' />, options)
      break
    case 'warning':
      toast.warning(<Msg content={text} status='warning' />, options)
      break
    case 'error':
      toast.error(<Msg content={text} status='error' />, options)
      break
    default:
      toast(<Msg content={text} status='button' {...buttons} />, options)
      break
  }
}

export const NotificationContainer = ToastContainer
